.tipps {
  margin-bottom: 100px;
}

.titleBlock {
  display: flex;
  gap: 80px;
  padding-bottom: 126px;
}

.title {
  font-size: 80px;
  font-weight: 500;
  color: #373434;
}

.subTitle {
  font-size: 24px;
  font-weight: 400;
  color: #373434;
  margin-top: 30px;
  max-width: 344px;
}

.cardsBlock {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 43px;
}

.card {
  border-radius: 20px;
  position: relative;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.card:hover {
  transform: scale(1.02);
}

.imageWrapper {
  position: relative;
  height: 240px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.image{
    margin-bottom: 16px;
    cursor: pointer;
}

