.text {
  font-size: 20px;
  font-weight: 400;
  margin-top: 16px;
  color: #373434;
}

/* media */
@media (max-width: 1240px) {
  /* адаптация под ноутбуки */
  .title{
    font-size: 70px;
    margin-left: 40px;
  }
  .subTitle{
    font-size: 22px;
    margin-top: 0;
  }
  .titleBlock{
    padding-bottom: 100px;
  }
  .cardsBlock{
    gap: 20px;
  }
}

